<template>
  <el-container id="app">
    <el-header></el-header>
    <el-container>
      <el-aside width="200px">
        <Nav/>
      </el-aside>
      <el-main>
        <router-view/>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
import Nav from "@/components/main/Nav";
export default {
  name: "App",
  components: {
    Nav
  }
};
</script>

<style scoped>
html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
}

.el-container {
  background-color: cornflowerblue;
  height: 100%;
}

.el-aside {
  height: 100%;
  padding-top: 20px;
}

.el-header {
  height: 50px;
}

.el-main {
  background-color: white;
  border-radius: 10px 10px 0 0;
  margin-right: 10px;
}

</style>



