import Vue from 'vue'
import Router from 'vue-router'
import FileServiceList from '@/components/file-list/FileServiceList'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'FileServiceList',
      component: FileServiceList
    }
  ]
})



